export function submitToAPI() {

    let URL = "https://mwwrpzj209.execute-api.us-east-1.amazonaws.com/Prod/contact-us";

    const emailInput = document.querySelector("#email-input");
    const nameInput = document.querySelector("#name-input");
    const phoneInput = document.querySelector("#phone-input");
    const descInput = document.querySelector("#desc-input");

    const emailButton = document.querySelector("#email-button");
    const formContainer = document.querySelector("#form-container");
    const validationMessage = document.querySelector("#validationMessage");

    if (emailInput.value == "") {
        validationMessage.innerHTML = "Please enter your email";
        return;
    }

    let reeamil = /^([\w-\.]+@([\w-]+\.)+[\w-]{2,6})?$/;
    if (!reeamil.test(emailInput.value)) {
        validationMessage.innerHTML = "Please enter valid email address";
        return;
    }

    let email = emailInput.value;
    let name = nameInput.value;
    let phone = phoneInput.value;
    let desc = descInput.value;

    emailInput.disabled = true;
    nameInput.disabled = true;
    phoneInput.disabled = true;
    descInput.disabled = true;
    emailButton.disabled = true;

    let data = {
        email: email,
        name: name,
        phone: phone,
        desc: desc,
        gRecaptchaResponse: window.gRecaptchaResponse
    };

    fetch(URL, {
        method: "POST",
        body: JSON.stringify(data),
        mode: "cors",
        headers: {
            "Content-Type": "application/json; charset=utf-8"
                // "Content-Type": "application/x-www-form-urlencoded",
        }
    }).then(res => {
        formContainer.innerHTML = `<div  class="mx-auto py-48">Thank you for your message! <br> We will get back to you as soon as possible</div>`;
    });

}

export function firstMonToFriNextMonth() {
    let d = new Date();
    d = new Date(d.getFullYear(), d.getMonth() + 1, 1);
    d.getDay() % 6 ? d : d.setDate((2 + d.getDay()) % 5);

    let weekday = new Array(7);
    weekday[0] = "Sunday";
    weekday[1] = "Monday";
    weekday[2] = "Tuesday";
    weekday[3] = "Wednesday";
    weekday[4] = "Thursday";
    weekday[5] = "Friday";
    weekday[6] = "Saturday";

    let month = new Array();
    month[0] = "January";
    month[1] = "February";
    month[2] = "March";
    month[3] = "April";
    month[4] = "May";
    month[5] = "June";
    month[6] = "July";
    month[7] = "August";
    month[8] = "September";
    month[9] = "October";
    month[10] = "November";
    month[11] = "December";

    function nth(d) {
        if (d > 3 && d < 21) return "th";
        switch (d % 10) {
            case 1:
                return d + "st";
            case 2:
                return d + "nd";
            case 3:
                return d + "rd";
            default:
                return d + "th";
        }
    }

    return month[d.getMonth()] + ", " + weekday[d.getDay()] + " " + nth(d.getDate());

}