<script>
  import { onMount } from "svelte";
  import parallax from "./parallax";
  import { submitToAPI, firstMonToFriNextMonth } from "./formAction";

  function startParallax() {
    let $ = document.querySelector.bind(document);

    let blob1 = $("#blob-1");
    let blob2 = $("#blob-2");
    let blob3 = $("#blob-3");
    let blob4 = $("#blob-4");

    parallax(
      (lastScrollY, prefix, pos, setTickingFalse) => {
        let relativeY = lastScrollY / 2000;
        let xmultiplier = window.innerWidth / 960;
        // red gradinet
        prefix(
          blob1.style,
          "Transform",
          "translate3d(" +
            500 * xmultiplier +
            "px," +
            pos(1500, -1400, relativeY, 0) +
            "px, 0)"
        );
        // green gradinet
        prefix(
          blob2.style,
          "Transform",
          "translate3d(" +
            600 * xmultiplier +
            "px," +
            pos(104, -1540, relativeY, 0) +
            "px, 0)"
        );

        // big  blue
        prefix(
          blob4.style,
          "Transform",
          "translate3d(" +
            -48 * xmultiplier +
            "px," +
            pos(704, -600, relativeY, 0) +
            "px, 0)"
        );

        // big  red
        prefix(
          blob3.style,
          "Transform",
          "translate3d(" +
            700 * xmultiplier +
            "px," +
            pos(-150, -500, relativeY, 0) +
            "px, 0)"
        );

        setTickingFalse();
      },
      function() {
        blob1.classList.add("force-show-sm");

        blob2.classList.add("force-show");
        blob3.classList.add("force-show");
        blob4.classList.add("force-show");
      },
      startParallax
    );
  }

  onMount(async () => {
    startParallax();

    // start recaptcha
    grecaptcha.ready(function() {
      grecaptcha
        .execute("6LcglZEUAAAAADpY_z6HlGSY-uJSqtMO-y2dQ83D", {
          action: "homepage"
        })
        .then(function(token) {
          window.gRecaptchaResponse = token;
        });
    });

    window.submitToAPI = submitToAPI;
  });

  import Nav from "../components/NavLightColors.svelte";
  import FooterContact from "../components/FooterContact.svelte";

  let nextslot = firstMonToFriNextMonth();
</script>

<style>

</style>

